<template>
  <div id="app">
    <header>
      <div class="top_hd">
        <ul>
          <li v-for="(item,index) in topMenu">
            <a href="javascript:" @click="Selpage(6,index)">
              <h3>{{item.title}}</h3>
              <p>{{item.value}}</p>
            </a>
          </li>
        </ul>
      </div>
      <mt-navbar v-model="selected" :class="['NavList',{fix:topVal}]">
        <mt-tab-item id="1" class="nav_title">国乐首页</mt-tab-item>
        <mt-tab-item id="2" class="nav_title">古筝专卖</mt-tab-item>
        <mt-tab-item id="3" class="nav_title">古琴专卖</mt-tab-item>
        <mt-tab-item id="4" class="nav_title">优惠不停歇</mt-tab-item>
        <mt-tab-item id="5" class="nav_title">来店路线</mt-tab-item>
      </mt-navbar>
    </header>
    <!-- tab-container -->
    <mt-tab-container v-model="selected">
      <mt-tab-container-item id="1">
       <index></index>
      </mt-tab-container-item>
      <mt-tab-container-item id="2">
        <Loading></Loading>
      </mt-tab-container-item>
      <mt-tab-container-item id="3">
        <SliderType :offsetT="topVal"></SliderType>
      </mt-tab-container-item>
      <mt-tab-container-item id="4">
      </mt-tab-container-item>
      <mt-tab-container-item id="5">
        <amap ></amap>
      </mt-tab-container-item>
      <mt-tab-container-item id="6">
        <SortType></SortType>
      </mt-tab-container-item>
      <mt-tab-container-item id="7">
        777777
      </mt-tab-container-item>
      <mt-tab-container-item id="8">
        888888
      </mt-tab-container-item>
    </mt-tab-container>
    <footer v-show="abc">
      <Foot></Foot>
    </footer>
    <nav>
      <ul class="bottom_nav">
        <li><a href="javascript:">
          <i class="gq"></i>
        <p>古筝专卖</p>
        </a></li>
        <li><a href="javascript:">
          <i class="gz"></i>
        <p>古琴专卖</p>
        </a></li>
        <li><a href="javascript:">
          <i class="ph"></i>
        <p>咨询热线</p>
        </a></li>
        <li class="active"><a href="tel:10086">
          <i class="msg"></i>
        <p>在线客服</p>
        </a></li>
      </ul>
    </nav>
    <button class="toTop" v-show="topVal" @click="toTop">top</button>
  </div>
</template>

<script>
  import $ from 'jquery'
  import { Navbar, TabItem } from 'mint-ui';
  import index from './components/pages/index.vue';
  import RightCon from '@/components/common/RightCon'
  import Foot from '@/components/common/Foot'
  import amap from '@/components/common/map'
  import SliderType from '@/components/pages/SliderType.vue';
  import SortType from '@/components/pages/SortType.vue';

export default {
  name: 'App',
  data(){
    return{
      topMenu:[
        {id:"01",title:"古筝专卖",value:"敦煌、鼎韵、月牙等"},
        {id:"02",title:"古琴专卖",value:"练习琴，名家琴"},
        {id:"03",title:"实体地址",value:"广州体育西路F出口"},
      ],
      topVal:false,
      selected:"1",
      isShow:true,
    }
  },
  methods:{
  onScroll(){
      if($(window).scrollTop()>=$('.top_hd').height()){
        this.topVal=true;
      }else{
        this.topVal=false;
      }
      },
    toTop(){
      let oBody=document.documentElement||document.body;
      $(oBody,"html").animate({scrollTop:0},500)
    },
    Selpage(num,nowIndex){
      num=parseInt(num);
      nowIndex=(nowIndex+num).toString();

      this.selected=nowIndex;
    }

  },
  components:{
    index,SliderType,RightCon,Foot,amap,SortType
  },
  computed:{
abc(){
      if(this.selected=="3"){
        this.isShow=false;
      }else{
        this.isShow=true;
      }
      return this.isShow
    }

//      if(this.selected==3){
//        this.isShow=false;
//      }else{
//        this.isShow=true;
//      }

  },
  mounted(){
    window.addEventListener("scroll",()=>{
      this. onScroll();
    })
  }
}


</script>

<style scoped lang="scss" >
  @media screen and (max-width: 640px){
    .nav_title{
      font-size: 18px;
    }
  }
  @media screen and (max-width: 480px){
    .nav_title{
      font-size: 27px;
    }
  }
  @media screen and (max-width: 320px){
    .nav_title{
      font-size: 36px;
    }
  }
  .mint-navbar .mint-tab-item.is-selected{
  color:#ffffff;
    border-bottom: none;
}
.fix{
  z-index: 2;
  max-width: 640px;
  width: 100%;
  position: fixed;
  top: 0;
}
#app {
  margin: 0 auto;
  max-width: 640px;
  width: 100%;
  background: #fff0f5;
  padding-top: 14px;
}
  .top_hd ul{
    display: flex;
  }
  .top_hd li{
    flex: 1;
    border-right: 1px solid slategrey;
  }
  .top_hd a{
    display: block;
    width: 100%;
    height: 100%;
    color: black;
    text-align: center;
  }
  .top_hd h3{
    font-family: huawen_kaiti;
    font-weight: bold;
    font-size: 30px;
  }
  .top_hd p{
    font-family: huawen_black;
    font-size: 20px;
  }
  .NavList{
    height: 44px;
    line-height: 44px;
    width: 100%;
    background: #c9391f;
  }
  .nav_title{
    font-size: 18px;
    color: #fff0f5;
  }
  nav{
    max-width: 640px;
    width: 100%;
    position: fixed;
    left: calc(50% - 320px);
    bottom: 0;
  }
  .bottom_nav{
    background: #f5f3e6;
    display: flex;
    width: 100%;
    height: 60px;
    border-top: 1px solid #dddddd;
  }
  .bottom_nav li{
    width: 140px;
    height: 100%;
    border-right: 1px solid #dddddd;
    text-align: center;
  }
  .bottom_nav a{
    display: block;
    width: 100%;
    height: 100%;
    padding: 5px;
    box-sizing: border-box;
    color: #000;
  }
  .bottom_nav .active{
    background: firebrick;
    flex-grow: 1;
  }
  .bottom_nav i{
    margin: 0 auto;
    display: block;
    width: 30px;
    height: 30px;
    border-radius: 50%;
    background-image: url("https://s1.ax1x.com/2018/07/23/PJgW7D.png");
    background-size: 400px 400px;
  }
  .gq{
    background-position: -111px -100px;
  }
  .gz{
    background-position: -162px -100px;
  }
  .ph{
    background-position: -264px -100px;
  }
  .msg{
    background-position: -216px -100px;
  }
  .active a{
    color: white;
  }
  .toTop{
    position: fixed;
    left: calc(50% + 160px );
    bottom: 150px;
    width: 80px;
    height: 80px;
    border: none;
    border-radius: 50%;
    background: darkmagenta;
    color: white;
  }
</style>



