<template>
  <div>
    <ul class="menu">
      <li v-for="(item,index) in navlist" @click="tab(index)">{{item.value}}</li>
    </ul>
  </div>
</template>

<script type="text/ecmascript-6">
    export default{
        data(){
            return {
              navlist:[
                {id:"01",value:"月牙",path:""},
                {id:"02",value:"鼎韵",path:""},
                {id:"03",value:"敦煌",path:""},
                {id:"04",value:"朱雀",path:""},
                {id:"05",value:"碧泉",path:""},
                {id:"06",value:"琴园",path:""},
                {id:"07",value:"龙凤",path:""},
                {id:"08",value:"金韵",path:""},
                {id:"09",value:"天艺",path:""}
              ]
            }
        },
    methods:{
      tab(index){
        this.$emit('onChange',index)
      }
    }
    }
</script>


<style scoped lang="scss">
  .menu{
    background: #fff;
  }
  .menu li{
    width: 160px;
    height: 45px;
    line-height: 45px;
    font-size: 18px;
    text-align: center;
    border: 1px solid #e3e3e3;
    cursor: pointer;
  }
  @media screen and (max-width: 640px){
    $width:18px;
    $height:45px;
    .menu{
      background: #fff;
    }
    .menu li{
      width: 160px;
      height: $height;
      line-height: $height;
      font-size: $width;
      text-align: center;
      border: 1px solid #e3e3e3;
      cursor: pointer;
    }
  }
  @media screen and (max-width: 480px){
    $width:27px;
    $height:60px;
    .menu{
      background: #fff;
    }
    .menu li{
      width: 160px;
      height: $height;
      line-height: $height;
      font-size: $width;
      text-align: center;
      border: 1px solid #e3e3e3;
      cursor: pointer;
    }
  }
  @media screen and (max-width: 320px){
    $width:36px;
    $height:90px;
    .menu{
      background: #fff;
    }
    .menu li{
      width: 160px;
      height: $height;
      line-height: $height;
      font-size: $width;
      text-align: center;
      border: 1px solid #e3e3e3;
      cursor: pointer;
    }
  }
.fix{
  position: fixed;
  top:44px
}

</style>



