<template>
<div class="MainBgColor">
  <div class="form">
    <i class="iconfont icon-return"></i>
    <span class="searchInput">
      <input type="text" value="" placeholder="敦煌"/>
      <i class="iconfont icon-search"></i>
    </span>
    <i class="iconfont icon-menu"></i>
  </div>
  <div class="banan_lt">
    <img src="../../assets/images/bananer/lt_ban/02.jpg" alt=""/>
  </div>
  <div class="MainSort">
    <ul>
      <li class="active" @click="Sort('newPrice')">销量</li>
      <li>上新</li>
      <li>价格</li>
      <li>评价</li>
    </ul>
  </div>
  <div>
    <productlist :products="products" :isShow="true"></productlist>
  </div>
</div>
</template>

<script type="text/ecmascript-6">
  import productlist from '@/components/common/productlist'
    export default{
        data(){
            return {
              sort:true,
              isShow:true,
              products:[
      {name:"月牙06山水知音",oldPrice:"1280",newPrice:"880",src:require("../../assets/images/products/base/01.png")},
      {name:"鼎韵903午后清芳",oldPrice:"680",newPrice:"580",src:require("../../assets/images/products/base/02.png")},
      {name:"敦煌694k蕉窗夜雨",oldPrice:"980",newPrice:"680",src:require("../../assets/images/products/base/03.png")},
      {name:"鼎韵903红木竹简",oldPrice:"1980",newPrice:"1780",src:require("../../assets/images/products/base/04.png")},
      {name:"朱雀古筝520古筝",oldPrice:"7280",newPrice:"6580",src:require("../../assets/images/products/best/01.png")},
      {name:"敦煌古筝双鹤朝阳",oldPrice:"380",newPrice:"180",src:require("../../assets/images/products/best/02.png")},
      {name:"月牙06山水知音",oldPrice:"1180",newPrice:"880",src:require("../../assets/images/products/best/03.png")},
      {name:"月牙06山水知音",oldPrice:"1680",newPrice:"1480",src:require("../../assets/images/products/best/04.png")}
    ]
            }
        },
  methods:{
    Sort(type){
      this.products.sort((n1,n2)=>{
        let a1=n1[type];
        let a2=n2[type];
        return a1-a2
      })
      if(!this.sort){
        this.products.reverse()
      }
      this.sort=!this.sort
    }
  },
  components:{
    productlist
  }
    }
</script>


<style scoped lang="scss">
.form{
  position: relative;
  width: 100%;
  height: 40px;
  line-height: 40px;
}
  .icon-return,.icon-search,.icon-menu{
    position: absolute;
    width: 40px;
    height: 40px;
    line-height: 40px;
    color: $font-color;
    font-size: 25px;
    text-align: center;
  }
.icon-return{
  left: 0;
  top: 0;
}
  .icon-search{
    width: 30px;
    height: 30px;
    right: 0;
    top: 0;
  }
  .icon-menu{
    top: 0;
    right: 0;
  }
  .searchInput{
    position: absolute;
    left: calc(50% - 200px);
    width: 440px;
    height: 30px;
    padding: 0;
    margin-top: 5px;
    border-radius: 3px;
    border:1px solid #666;
    background: #fff;
    overflow: hidden;
  }
  .searchInput input{
    display: block;
    width: 400px;
    height: 28px;
    line-height: 28px;
    padding: 1px;
    border: none;
    text-align: left;
    color: $font-color;
    white-space: nowrap;
    text-overflow: ellipsis;
  }

  .banan_lt{
    width: 100%;
    height: 210px;
  }
  .banan_lt{
    width: 100%;
    height: 100%;
  }
  .MainSort ul{
    display: flex;
    border-bottom: 1px solid #c7aa5a;;
  }
  .MainSort li{
    flex: 1;
    height: 40px;
    left: 40px;
    font-size: 22px;
    color: #c7aa5a;
    text-align: center;
  }
  .MainSort .active{
    color: #d60f0f;
    border-bottom: 5px solid #d60f0f;
  }
</style>



